
const KeyDownInputs: Array<String> = [ ];
const KeyUpInputs: Array<String> = [ ];

export class KeyboardInput {

	constructor() {
		window.addEventListener("keydown", this.onKeyDown);
		window.addEventListener("keyup", this.onKeyUp);
	}

	onKeyDown(evt: KeyboardEvent) {
		const keyUpIndex = KeyUpInputs.indexOf(evt.code);
		if (keyUpIndex > -1) {
			KeyUpInputs.splice(keyUpIndex, 1);
		}
		if (KeyDownInputs.indexOf(evt.code) === -1) {
			KeyDownInputs.push(evt.code);
		}
	}

	onKeyUp(evt: KeyboardEvent) {
		const keyDownIndex = KeyUpInputs.indexOf(evt.code);
		if (keyDownIndex > -1) {
			KeyUpInputs.splice(keyDownIndex, 1);
		}
	}

	static GetKeyDown(key: String): Boolean {
		return KeyDownInputs.indexOf(key) !== -1;
	}

	static GetKeyUp(key: String): Boolean {
		return KeyUpInputs.indexOf(key) !== -1;
	}
}

export default new KeyboardInput();